  z-index: 5;
  transition: margin-left .3s ease-in-out;
  position: absolute;
}

.header__link-b {
  padding-bottom: 8px;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header__link-b:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: currentColor;
  display: block;
  position: absolute;
  bottom: 0;
  transform: scaleX(0);
}

.header__link-b:hover:before {
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  transform: scaleX(100%);
}

