.reviews__item {
  cursor: pointer;
  background: top / contain no-repeat;
  border: 1px solid #5b86a1;
  border-top: none;
  margin: 0 auto;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.reviews__item:hover {
  border: none;
  box-shadow: 0 0 85px 6px #a3a5a3c4;
}

.reviews__item-bg {
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  background: center / cover no-repeat;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateY(90deg);
}

.reviews__item:hover .reviews__item-bg {
  opacity: 1;
  visibility: visible;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  transform: rotateY(0deg);
}

.reviews__title {
  color: #5b86a1;
  text-align: center;
  font-weight: 600;
}

.reviews__title:before {
  content: "";
  background-size: contain;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
}

.reviews__title + p {
  font-size: 20px;
}

.maria-title:before {
  background-image: url("maria.4fb60cd3.png");
}

.ostap-title:before {
  background-image: url("ostap.07ae0231.png");
}

.anita-title:before {
  background-image: url("anita.36107a4b.png");
}

.reviews__icon-around {
  width: 40px;
  height: 40px;
  z-index: -1;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.reviews__icon-around > .reviews__icon {
  width: 32px;
  height: 32px;
  fill: #5b86a1;
