.backdrop-hero {
    position: fixed;
    top: 0;
    @include size(100%);

    background-color: rgba(32, 35, 33, 0.60);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), 
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 99;
}

.modal-hero {
    text-align: center;
    @include position(100);
    background-color: getColor('primary');
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backdrop-hero.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    overflow: visible;
}

.btn-close {
    cursor: pointer;
    position: absolute;
    @include size(40px);
    @include flex(center, center);
    border: none;
    background-color: inherit;

    & > svg {
        @include size(30px);
        fill: getColor('black-secondary');
        transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    & > svg:hover {
        @include size(30px);
        fill: getColor('accent');
        transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
    }
}

.title.modal-hero__title {
    margin: 0;
}

.checkbox-wrapper {
    @include flex(start);
    flex-direction: column;
}

.modal-hero__input {
    padding: 8px;
    border: none;
    border-bottom: 1px solid getColor('input-border-color');
    background-color: inherit;
}

.modal-hero__check {
    @include flex(center);

    & > input {
        margin-right: 14px;
        @include size(20px);
    }
}

@media screen and (min-width: 320px) and (max-width: 1439px) {
    .modal-hero__description span{
        display: block;
    }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .modal-hero {
        font-size: getSize('little');
        @include size(100vw, $height: 504px);
        padding: 52px 32px 36px 32px;
    }

    .form-wrapper > button {
        width: 100%;
    }

    .title.modal-hero__title {
        font-size: getSize('large');
        margin-bottom: 20px;
    }

    .modal-hero__description {
        margin-bottom: 24px;
    }

    .modal-hero__input {
        width: 100%;
        margin-bottom: 16px;
    }

    .checkbox-wrapper {
        margin-bottom: 26px;

        & .modal-hero__check:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    .btn-close {
        top: 12;
        right: 12;
    }
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
    .modal-hero {
        font-size: getSize('little-small');
        @include size(478px, $height:581px);
        padding: 60px 42px 40px 42px;
    }
    
    .title.modal-hero__title {
        font-size: getSize('more-lage');
        margin-bottom: 24px;
    }

    .modal-hero__input {
        width: 394px;
        margin-bottom: 18px;
    }

    .checkbox-wrapper {
        margin-bottom: 32px;

        & .modal-hero__check:not(:last-child) {
            margin-bottom: 24px;
        }
    }

    .btn-close {
        top: 20;
        right: 20;
    }
}

@media screen and (min-width: 768px) {
    .modal-hero__description {
        margin-bottom: 32px;
    }
}

@media screen and (min-width: 1440px) {
    .modal-hero {
        font-size: getSize('large');
        @include size(774px, $height: 745px);
        padding: 116px 100px 60px 100px;
    }

    .title.modal-hero__title {
        font-size: getSize('more-big');
        margin-bottom: 28px;
    }

    .modal-hero__input {
        width: 575px;
        margin-bottom: 20px;
    }

    .checkbox-wrapper {
        margin-bottom: 44px;

        & .modal-hero__check:not(:last-child) {
            margin-bottom: 24px;
        }
    }

    .btn-close {
        top: 40;
        right: 40;
    }
}