.swiper {
  @include size(100%);
}

.swiper-container {
  display: flex;
  margin: 0 auto;
}

.swiper-slide {
  @include flex(center, center);
}

.swiper-slide img {
  display: block;
  object-fit: contain;
  width: 100%;
}

.gallery__button {
  @include flex(center, center);
}

.gallery__button-next,
.gallery__button-prev {
  cursor: pointer;
  border: none;
  background-color: inherit;
} 

.swiper-svg {
  fill: getColor('input-border-color');
}

.gallery__button-prev:hover .swiper-svg,
.gallery__button-next:hover .swiper-svg {
  fill: getColor('accent');
} 

@media screen and (min-width:320px) and (max-width: 1439px) {
  .gallery__button-next {
    margin-left: 20px;
  }

  .gallery__button-prev {
    margin-right: 20px;
  }

  .gallery__button-prev > .swiper-svg,
  .gallery__button-next > .swiper-svg {
    @include size(15px, $height:27px);
  }
}

@media screen and (min-width:320px) and  (max-width: 767px) {
  .gallery__button-next,
  .gallery__button-prev {
    @include size(28px);
  }
}

@media screen and (min-width: 375px) and (max-width: 767px) {
  .swiper-slide img {
    object-fit: cover;
    min-width: 280px;
  }

  .swiper-wrapper {
    margin-bottom: 24px;
  }

  .gallery {
    margin-bottom: 52px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
  .swiper-slide img {
    height: 275px;
  }

  .swiper-wrapper {
    margin-bottom: 32px;
  }

  .gallery__button-next,
  .gallery__button-prev {
    @include size(32px);
  }
}

@media screen and (min-width: 768px) {
  .gallery {
    margin-bottom: 80px;
  }
}

@media screen and (min-width: 1440px) {
  .swiper-slide {
    width: 1170px;
  }

  .swiper-slide img {
    height: 360px;
  }

  .gallery {
    width: 1440px;
  }

  .swiper-wrapper {
    margin-bottom: 40px;
  }

  .upcomingSwiper > .swiper-wrapper {
    margin-bottom: 0;
  }

  .gallery__button-next,
  .gallery__button-prev {
    @include size(40px);
    padding: 0;
  }

  .gallery__button-next {
    margin-left: 30px;
  }

  .gallery__button-prev {
    margin-right: 30px;
  }

  .gallery__button-prev > .swiper-svg,
  .gallery__button-next > .swiper-svg {
    @include size(25px, $height: 37px);
  }
}